@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Image";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Layout/Padding";
@use "@app/bancor-components/Style/Font";

.chooseContacts {
    > header {
        & #{Text.$selector} {
            @include Margin.set($bottom: alpha);
            @include Font.set($size: iota, $weight: bold);
        }
        & #{Image.$selector} {
            @include Image.asModoLogo;
        }
    }
    & #{Text.$selector} {
        word-break: break-word;
    }
    #{Form.$selector} {
        @include Margin.set($top: beta, $bottom: beta);
        & > #{Button.$selector} {
            @include Margin.set($top: beta);
        }
    }
    .buttonShowMore {
        @include Margin.set($top: beta, $bottom: beta);
    }
    .contactBox {
        & #{Text.$selector} {
            word-break: keep-all;
        }
        &:hover {
            @include Color.set($bg: "gray-400");
        }
        @include Color.set($bg: "gray-200");
        @include Flex.set($justify: space-between, $align: center);
        @include Padding.set($preset: alpha);
        @include Border.set($style: none, $color: "gray-500", $width: alpha, $style-b: gamma);
        width: Layout.get-max-width(alpha);
        .avatarNameBox {
            @include Flex.set($align: center);
            overflow: hidden;
            .avatar {
                @include Margin.set($right: beta);
                @include Flex.set($shrink: none);
            }
            .contactName {
                @include Margin.set($right: beta);
                @include Font.set($space: nowrap);
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
    .modal {
        h4 {
            @include Margin.set($top: alpha);
        }
    }
    .footer {
        @include Flex.set($dir: column);
        width: Layout.get-max-width(alpha);
        #{Button.$selector} {
            @include Margin.set($bottom: alpha);
        }
    }
}
